import Button from 'react-bootstrap/Button'
import MenuList from './MenuList';
import App from '../App';



function MenuItem({ item, description, type, price, addToOrder}){

const handleClick = () => {
       
         const menuItem = {
            item, 
            price,
         }
        addToOrder(menuItem);
        
     }

return (
    <li className="menu list-group-item d-flex justify-content-between lh-condensed item" type={type}>
        <div className="col-md-10">
            <h4 className="item-title col-md-8 mx-1">{item}</h4>
            <div className="item-detail mx-3 fst-italic text-muted">{description}</div>
        </div>
        <div className="col-md-2 item-price-section">
            <h4 className="item-price mt-3">${price}</h4>
            <Button variant="outline-primary" size="sm" type="button" onClick={handleClick}>Add to cart</Button>
        </div>
    </li>
    
)

}   
export default MenuItem