import "../App.css";
import { setOrder } from "../App";
import MenuItem from "./MenuItem";
import Button from "react-bootstrap/Button";
import { toBeEmptyDOMElement } from "@testing-library/jest-dom/dist/matchers";
import { useEffect } from "react";

const TEMP_ITEM = [
   {
      id: 1,
      item: "raisin",
   },
   {
      id: 2,
      item: "cherry",
   },
   {
      id: 3,
      item: "apple",
   },
];

function Order({ order, setOrder }) {
   const deleteItem = (index) => {
      const updatedOrder = [...order];
      updatedOrder.splice(index, 1);
      setOrder(updatedOrder);
      console.log("item to remove", order);
   };

   const calculateTotal = () => {
      return order.reduce((total, item) => total + item.price, 0);
   };

   const saveOrder = () => {
      localStorage.setItem("order", JSON.stringify(order));
   };

   const orderHTML = order.map((item, index) => (
      <li
         key={index}
         className="list-group-item d-flex justify-content-between lh-condensed"
         id="order-list"
      >
         <div>
            <h6 className="my-1">{item.item}</h6>
         </div>
         <span id="price">{item.price}</span>
         <Button
            variant="outline-danger"
            size="sm"
            type="button"
            id="delbutton"
            className="mx-0"
            onClick={() => deleteItem(index)}
         >
            Delete
         </Button>
      </li>
   ));
   return (
      <>
         <ul>
            {orderHTML}
            <div className="d-flex ">
               <div className="text-muted d-flex " id="total">
                  <p>Total:${calculateTotal().toFixed(2)}</p>
               </div>
               <Button
                  variant="outline-success"
                  id="save-button"
                  type="button"
                  className="p-2 ms-4"
                  onClick={saveOrder}
               >
                  Save Order
               </Button>
            </div>
         </ul>
      </>
   );
}
// export { calculateTotal };

export default Order;
